.card{
  display: grid;
  max-width: 150px;
  margin: 0 auto;
  row-gap: 8px;
  position: relative;
}

.card__name{
  margin: 0;
  text-align: center;
  grid-column: 1/3;
}

.card__counter{
  position: absolute;
  right: 10px;
  top: 10px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: rgb(204, 174, 6);
  text-align: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 30px;
}

.card__img{
  height: 150px;
  width: 150px;
  border-radius: 30px;
  grid-column: 1/3;
}

.card__price{
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: var(--tg-theme-text-color);
  grid-column: 1/2;
}

.cards__price_line{
  text-decoration: line-through;
  color:rgb(187, 187, 187);
}

.card__price_discount{
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: var(--tg-theme-text-color);
  grid-column: 2/3;
}

.card__buttons{
  display: grid;
  grid-column: 1/3;
}

.card__buttons_columns{
  grid-template-columns: repeat(2, 1fr);
  column-gap: 8px;
}

.card__button{
  display: none;
  border-radius: 10px;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
}

.card__button_minus{
  display: block;
  background-color: rgb(204, 52, 6);
}

.card__button_add{
  display: block;
  background-color: rgb(204, 174, 6);
}
