.header{
  margin: 5px 0 15px;
}

.header__swiper{
  display: grid;
  justify-content: center;
}

.swiper-slide{
  width: 120px;
  display: grid;
  justify-content: center;
  align-content: center;
  
}

.swiper-slide:first-child{
  margin-left: 10px;
}

.header__link{
  display: grid;
  justify-content: center;
  align-content: center;
  height: 40px;
  width: 120px;
  text-decoration: none;
  background-color: #edf0f6;
  border-radius: 20px;
  text-align: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  color: black;
}

.header__link_active{
  background-color: #1277ba;
  color: white;
}

@media (max-width: 1370px){
  .header__swiper{
    display: grid;
    justify-content: start;
  }
}
